import { ThreeDots } from "react-loader-spinner";

export function LoadingComponent({ secondary_text }) {
  return (
    <div className="m-auto max-w-4xl flex flex-col justify-center items-center py-48 my-2 gap-4">
      <div className="flex flex-col justify-center items-center gap-4">
        <span className="text-3xl font-bold">Please be patient</span>
        <span className="text-xl">{secondary_text}</span>
      </div>
      <ThreeDots visible width="40" height="20" color="#000" />
    </div>
  );
}
