import { IoLocationSharp } from "react-icons/io5";

export function Pickupdropoff({ pickup_dropoff_route }) {
  return (
    <div className="mb-1 border border-gray-200 p-2 rounded-md">
      <div className="flex flex-row items-start gap-1">
        <div>
          <IoLocationSharp size={18} className="text-gray-400" />
        </div>
        <div className="flex flex-col gap-1">
          <p className="text-sm font-bold">
            {pickup_dropoff_route.primary_text},{" "}
            <span className="font-normal">
              {pickup_dropoff_route.secondary_text}
            </span>
          </p>
          <p className="text-xs text-gray-400">
            {String(pickup_dropoff_route.route_detail).length > 0 && (
              <span className="font-bold">Message:</span>
            )}{" "}
            {pickup_dropoff_route.route_detail}
          </p>
        </div>
      </div>
    </div>
  );
}
