// import { PricingContent } from "./booking_content/booking_pricing_content";
import { BookingDetailsContent } from "./booking_content/booking_details_content";
import { cn } from "../../utils/custom_functions";

export function BookingCard({ booking, ...props }) {
  // console.log(booking);
  return (
    <div className="rounded-md pb-6">
      <h2
        className={cn("text-start text-slate-800 px-2 font-extrabold text-lg", {
          "text-red-500": booking?.booking_canceled,
          "text-gray-200": booking?.booking_completed,
        })}
      >
        {booking?.id > 0 && <div className="pt-2" />}
        {booking?.id > 0 && <span>Invoice {booking?.id}</span>}

        {booking?.booking_canceled && (
          <p className="text-red-500 text-sm">Booking canceled</p>
        )}
        {booking?.booking_completed && (
          <p className="text-gray-200 text-sm">Booking Completed</p>
        )}
      </h2>
      {/*Booking content card*/}
      <BookingDetailsContent booking={booking} />
      {/* <div className="py-1" /> */}
      {/*Booking content card*/}
      {/*Edit booking*/}
      {props.children}
      {/*Edit booking*/}
    </div>
  );
}
